



body{
    margin: 0px;
    padding: 0px;
    font-size: 62.5%;
}

  nav ul{
    display:flex;
    margin:0px;
    padding: 0px;
    justify-content: space-between;
    background-color: rgb(90, 84, 84);
    float:left;
    width:100%;
     height:10%;
    list-style-type: none;
    position:relative;
}
nav ul li{
    float: left;
}
.hv{
    color:whitesmoke;
}
.hv:hover{
    color:blue;
}
nav ul li a{
    text-decoration:none;
    display: inline-block;
    margin-left: 0px;
    padding: 5px;
    box-sizing: border-box; 
    font-size: 200%;
    width:100%;
   
    color:white;
   
}
#menu ul{
    
    margin:0px;
    padding: 0px;
    background-color: rgb(73, 71, 71);
    float:left;
    width:20%;
    height:100%; 
    list-style-type: none;
    position: absolute;
    left:0%;
    top:10%;
}


#menu ul li a{
    text-decoration:none;
    display: inline-block;
    background-color: rgb(12, 11, 11);
    margin-left: 0px;
    padding: 5px;
    box-sizing: border-box; 
    font-size: 150%;
    width:100%;
    height:4rem;
    border-bottom:1.5px solid white;
    color:rgb(247, 243, 243);
   
}
#menu ul li a:hover{
    background-color: chartreuse;
}
#menu ul li a:active{
   
    background-color:green;
}
.b1{
    display: flex;
}
.a1{
    margin-right:15px;
}
.a2{
    margin-top: 20px;
}
.box{
    display:flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin:0px;
    padding: 0px;
    background-color: rgb(240, 230, 230);
    height: 33%;
    width:79vw;
    position: absolute;
    left:20%;
    top:10%;
    
   
}
.box .b2 .f{
    
    margin-right: 10px;
}
textarea{
        margin-top:1rem;
}

.box .b1 #btn{
   
    position: absolute;
    
    right:22%;
    top:10%;
}
.bt{
    margin-right:2rem;
}

