.ftr{
    
    height:10rem;
    width:20rem;
    overflow:scroll;
    position: absolute;
    left:22%;
    top:55%;

}

.bftr ul{
    margin:0px;
    padding: 0px;
    background-color: rgb(221, 212, 212);
    width:79%;
    height:10%;
     overflow-y: hidden;
    list-style-type: none;
    position: absolute;
    left:20%;
    top:44%;
}
.bftr ul li{
    float: left;

}
.bftr ul li a{
    text-decoration:none;
    display: inline-block;
   
    padding: 0px;
    box-sizing: border-box; 
        width: 6rem;
    border-right: 2px solid rgb(19, 18, 18);
    color:grey;
    
}
.bftr ul li a:hover{
    color:rgb(27, 27, 27);
    font-size: larger;
}
table,th,td{
    border:1px solid black;
    border-collapse: collapse;
}
th{
    background-color: cyan;
}
td:nth-child(1){
    color:blue;
}
td:nth-child(2){
    color:red;
}
td:nth-child(3){
    color:green;
}
@media only screen and (max-width: 600px) {
    html{
        font-size: 50%;
    }

    .box .b1 #btn{
        position: absolute;
        left:22%;
        top:14%;
    }
  }




